.cover-container {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.cover {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url('../images/nebula.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.words-container {
  padding: 50px;
  background-color: rgba(0, 0, 0, .5);
  border: 1px solid white;
}

.words {
  display: block;
  color: #FFF;
  font-size: 500%;
  text-align: center;
  z-index: 10;
}

.other-words {
  display: block;
  color: #FFF;
  font-size: 150%;
  text-align: center;
  z-index: 10;
}
  
/* Responsive words */
@media screen and (max-width: 480px) {

  .words-container {
    padding: 25px;
  }

  .words {
    font-size: 250%;
  }

  .other-words {
    font-size: 100%;
  }
}



