.tile-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 30%;
  height: 350px;
  background-color: #505050;
  border: solid white 2px;
  border-radius: 10%;
}

@media screen and (max-width: 500px) {
  .tile-container {
    margin: 3%;
    width: 90%;
    height: 250px;
  }
}



