.overview-container {
  position: relative;
  width: 100vw;
  background-color: #000;
  border-top: 2px solid #00f0ff;
  border-bottom: 2px solid #00f0ff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: 5%;
}

.title {
  font-size: 50px;
  color: #FFF;
  text-align: center;
  z-index: 20;
}

.subtitle {
  font-size: 30px;
  color: #FFF;
  text-align: center;
  z-index: 20;
}

.tiles-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 90%;
  padding-bottom: 100px;
}

@media screen and (max-width: 500px) {
  
  .title {
    font-size: 40px;
  }

  .subtitle {
    font-size: 20px;
  }


  .tiles-container {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    padding-bottom: 40px;
  }
}


